n. ")]),_c('p',[_vm._v(" Ballet gives you a qualitative \"smell-test\", an extra data-point to make your best-informed decision before interacting with an untrusted wallet. By looking at their qualitative track-record, there is no need for \"official identity\" to get that sense of \"personality\" in the same way I can get a first impression of a person by looking at them without the need to know their name or see their passport. I think that's really cool. ")]),_c('h3',[_vm._v("2. Find Better Questions")]),_c('p',[_vm._v(" This is when you don't yet know what you're looking for or you just want to get to better understand how Algorand works (technically) and how it's going (ecologically) by taking a stroll down main street. ")]),_c('p',[_vm._v(" Ballet supports this kind of inductive observation and experimentation by simply diving in with a random Account ID, even your own, switching between focuses and layouts, untangling some knots and clicking on other nodes. ")]),_c('p',[_vm._v(" By just futzing around I've had several moments of \"Heh!?\" that piqued my curiosity and led me to asking much more specific, and frankly, more interesting questions ")]),_c('h2',[_vm._v("The benefit to the ecosystem")]),_c('p',[_vm._v(" Ballet gives us an additional, more qualitative, window to have \"more eyes on the sidewalk\"[1] in an effort to make Algorand a safe place for its inhabitants and visitors alike. Just like the eyes of neighbors, shop-keepers and even visitors keep a sidewalk safe by simply being there. ")]),_c('p',[_vm._v(" But why should you personally care about the safety of the ecosystem? ")]),_c('p',[_vm._v(" Let's say you want to invest into the biggest ROI artistic NFT collection today, and if we imagine Algorand (eventually) being like a big city very much like New York City: ")]),_c('p',[_vm._v(" I believe you shouldn't be looking for NFTs at all. You should be looking to meet Andy Warhol in 1951, when he was designing shoes for Miller, and a year before his first solo-show. ")]),_c('p',[_vm._v(" Now, for Andy Warhol's print-work (and anything else he touched really, including payments slips, scribbles and letters) to eventually become \"A Warhol\", it required New York City to be in its best shape, flush with post-war cash and rock-and-roll hitting the airwaves. ")]),_c('p',[_vm._v(" You needed a (relatively) safe city for artists like Andy Warhol to settle and take liberty to produce their best work. ")]),_c('p',[_vm._v(" That's why you should care about the safety of the ecosystem. ")]),_c('p',[_vm._v(" Because only then will the true heir to Andy Warhol settle in and grow his work. ")]),_c('p',[_vm._v(" And only then will you be there, on June 16th 1952, at Hugo Gallery, on East 55th Street and Madison Avenue, at Andy's first show. To build meaningful relationships, to witness history and, yes, to buy the first Warhols for a penny and a dime. ")]),_c('h2',[_vm._v("Yes, AND...")]),_c('p',[_vm._v(" I believe, all benefits stated above re-inforce each other: What's good for you personally is also good for the ecosystem. And vice-versa. ")]),_c('p',[_vm._v(" I also believe it's using \"qualitative AND quantitative\" methods equally to have your \"eyes on the sidewalk\" and \"your ears to the ground\". That's why you can click-hold on any Account, Asset, Application, Group and Transaction inside Ballet to open its information on AlgoExplorer in a new window. ")]),_c('h2',[_vm._v("PS")]),_c('p',[_vm._v(" 1. Just to make it clear, this is a personal project of mine. I built it because, after going on an actual adventure and wanting to spend a few Algos on NFTs, I felt deeply unsettled how much overt scamming and how little transparency there was. I did NOT FEEL SAFE while trying to buy a single NFT for fun because I had no way of understanding WHO the person (or group) behind the artwork really is. And I had no way to verify if the story they're telling me (The art-market is a great example of how important the authenticity of the story is for its value-creation[^3]) is actually true. And I had no way to know the people running the \"gallery\" itself. ")]),_c('p',[_vm._v(" 2. I decided to release the application "),_c('a',{attrs:{"href":"https://github.com/akaalias/algorand-ballet","target":"_blank"}},[_vm._v("fully open-source and under a permissive license")]),_vm._v(" for you to futz around with it and to allow for any ad-hoc security review in case you are unsure if you can trust the website or app. ")]),_c('h2',[_vm._v("Why \"Ballet\"?")]),_c('p',[_vm._v(" The inspiration came from Jane Jacobs, who keenly observed that when New York sidewalks were safe, they gave stage for an... "),_c('em',[_vm._v(" \"intricate ballet in which the individual dancers and ensembles all have distinctive parts which miraculously reinforce each other and compose an orderly whole.\" ")])]),_c('h2',[_vm._v("Questions?")]),_c('p',[_vm._v(" Let's talk If you have questions the best way to reach me is to talk in person: In person, let's get a coffee and take a walk! or via video/audio call on Zoom To make that easy, "),_c('a',{attrs:{"href":"https://calendly.com/alexis-rondeau","target":"_blank"}},[_vm._v("here's my Calendly where you can book a time for us to talk")]),_vm._v(". ")])]),_c('v-col',{attrs:{"cols":"3"}})],1)],1),_c('v-tab-item',{key:2},[_c('v-row',[_c('v-col',{attrs:{"cols":"3"}}),_c('v-col',{attrs:{"cols":"6"}},[_c('h1',{staticClass:"heading"},[_vm._v("Many thanks go out to")]),_c('h2',[_vm._v("PureStake")]),_c('p',[_vm._v(" PureStake have enabled me to iterate incredibly fast by "),_c('a',{attrs:{"href":"https://www.purestake.com/technology/algorand-api/"}},[_vm._v("providing their API to the Algorand blockchain")]),_vm._v(". It's been a bliss to work with it and I couldn't have done it without them. And I would immediately use their API again for any new project and recommend you try it out as well. (Please note that I am not affiliated with PureStake.) ")]),_c('h2',[_vm._v("Algorand Fo